let serviceTermsUrl = "https://www.yinxiang.com/legal/tos.php/";
let privacyTermsUrl = "https://www.yinxiang.com/privacy/";
let reqKey = "d967a1fdc3cd9d7eaf508fde28624ecd";
let lang = 'zh';//语言环境，移动端需要支持英文
let isMobile = true;//是否是移动端
const ACCOUNT_TYPE_PHONE = 0;
const ACCOUNT_TYPE_EMAIL = 1;
const ACCOUNT_TYPE_TEXTUTIL_KEYMAP = {};
ACCOUNT_TYPE_TEXTUTIL_KEYMAP[ACCOUNT_TYPE_PHONE] = 'phoneNumber';
ACCOUNT_TYPE_TEXTUTIL_KEYMAP[ACCOUNT_TYPE_EMAIL] = 'email';
const HELP_PAGE_LINK = 'https://help.yinxiang.com/hc/articles/71794';
const PASSWORD_REGEX = /^[A-Za-z0-9!#$%&'()*+,.\/:;<=>?@^_`{|}~\[\]\\-]{6,64}$/

const {protocol, host} = window.location;
const isDev = process.env.NODE_ENV === 'development';
const defaultDevelopmentApiHost = 'stage.yinxiang.com';
const defaultProductionApiHost = 'app.yinxiang.com';
const SIMPLE_PHONE_REG = /^1\d{10}$/;
const VERCODE_REG = /^\d{6}$/;
const BRIGE_CALLBACK_NAME_FOR_GET_REMAINING_TIME = 'remainingTimeCallback';

function setMobileEnv(isMobileEnv) {
    isMobile = isMobileEnv;
}

function getMobileEnv() {
    return isMobile;
}

function setLangEnv(language) {
    lang = language;
}

function getLangEnv() {
    return lang;
}

function extractApiHost(host) {
    const reg = /^(static\.)?(.+)/;
    let extractedApiHost;
    try {
        const result = host.match(reg);
        extractedApiHost = result[2];
    } catch (err) {
        console.error(err);
    }
    return extractedApiHost;
}

const apiHost = `${isDev ? defaultDevelopmentApiHost : extractApiHost(host) || defaultProductionApiHost}`;
const domain = `${isDev ? '' : extractApiHost(host) || defaultProductionApiHost}`;
const baseUrl = `${protocol}//${apiHost}`;
let loginUrl = baseUrl + "/Login.action";

export default {
    baseUrl,
    serviceTermsUrl,
    privacyTermsUrl,
    loginUrl,
    reqKey,
    setLangEnv,
    getLangEnv,
    getMobileEnv,
    setMobileEnv,
    ACCOUNT_TYPE_PHONE,
    ACCOUNT_TYPE_EMAIL,
    HELP_PAGE_LINK,
    ACCOUNT_TYPE_TEXTUTIL_KEYMAP,
    apiHost,
    domain,
    PASSWORD_REGEX,
    SIMPLE_PHONE_REG,
    VERCODE_REG,
    BRIGE_CALLBACK_NAME_FOR_GET_REMAINING_TIME,
}
